import { createApp } from 'vue'
import App from './App.vue'
//import router from './router'

import Popper from "vue3-popper"
import Toast from 'vue-toastification'
import 'vue-toastification/dist/index.css'
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'
import VCalendar from 'v-calendar';
import 'v-calendar/style.css';
import axios from 'axios'
//만끽 연동
import webviewBridge from '@/js/webviewBridge'
import wB from '@/js/wB'



/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret, faArrowLeft, faBell, faPlay, faStop, faCircleNotch, faPlus, faChevronDown,faChevronUp, faXmark,faRotateRight, faWater, faTemperature3, faClock } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* 위에서 import한 아이콘들을 Core library에 등록 */
library.add(faUserSecret,faArrowLeft, faBell, faPlay, faStop, faCircleNotch, faPlus,faChevronDown, faChevronUp,faXmark, faRotateRight, faWater, faTemperature3, faClock)

//axios.defaults.baseURL ="http://13.125.231.37:8080/"
const app = createApp(App)
app.component('font-awesome-icon',FontAwesomeIcon)
app.config.productionTip=false
app.config.globalProperties.$http = axios;
app.config.globalProperties.$loading = 0;

app.component('VueSlider', VueSlider)
app.component('Popper',Popper)
app.use(Toast).use(VCalendar, {})
//로컬 화면에서 테스트를 위해 임시로 주석처리
//app.use(webviewBridge) 
app.use(wB)

setTimeout(function() {app.mount('#app')}, 100);
