<template>
  <!--예약 모달창-->
  <div>
    <ReservePopup
      v-if="isReservePopup == true"
      class="reserve-popup"
      @offPopup="offPopup"
      @sendReserve="sendReserve"
      :allreserveArr="this.allreserveArr"
      :allreserveArrKey="this.allreserveArrKey"
      :toast="this.toast"
    />
    <div id="appview">
      <!--상단바-->
      <div id="topbar">
        <div id="apptitle">
          <!--<a href="https://mzoin.com/mankik/appMain"><font-awesome-icon :icon="['fasr', 'arrow-left']"/></a>-->
          <font-awesome-icon id="return_btn" :icon="['fasr', 'arrow-left']" @click="goMankik"/>
          <span>스마트바스</span>
        </div>
        <div id="topbar_menu">
          <font-awesome-icon id="mk_history_btn" :icon="['fas', 'bell']" style="display: none; color: #ff9900" @click="showHistoryList"/>
          <ul id="user_profile">
            <li v-for="i in userArr" :key="i">
              <Popper hover="true" arrow>
                <img :src="require(`@/assets/img/${i.img}.png`)" />
                <template #content>
                  <div class="popover_name">김성민</div>
                  <div class="popover_content">
                    <div class="email">직업</div>
                    <div class="email_detail">대학생</div>
                  </div>
                  <div class="popover_content">
                    <div class="email">학과</div>
                    <div class="email_detail">컴퓨터공학과</div>
                  </div>
                  <div class="popover_content">
                    <div class="email">이메일</div>
                    <div class="email_detail">rlatjdals369@naver.com</div>
                  </div>
                </template>
              </Popper>
            </li>
          </ul>
        </div>
      </div>
      <!--목욕 시작 버튼 및 현재 온도-->
      <div id="bath_container">
        <div>
          <button id="bath_button" v-on:click="bathButtonClick">
            <font-awesome-icon
              v-bind:icon="bathbuttonIcon"
              :class="bathbuttonIcon_spin"
            />
            {{ bathbuttonText }}
          </button>
        </div>
        <div>
          <span id="curr_temp">{{ bathState.temp }}</span>
          <span id="temp_sign">°C</span>
        </div>
      </div>
      <!--욕조 이미지-->
      <div id="bath-img">
        <img :src="bathImg" />
      </div>
      <div id="bathState-text">{{ bathStateText }}</div>
      <div id="update_time">{{ updateTime }}</div>
      <font-awesome-icon
        icon="rotate-right"
        id="refresh-btn"
        v-on:click="repeatRefresh"
      />
      <!--
      <button @click="imgChange">욕조 이미지 변경 테스트용</button>
      -->
      <!--<button @click="test">이력 테스트용</button>
-->
      <!--탭 뷰-->
      <div id="tab">
        <div id="tabButton">
          <button v-on:click="currentTab = 'ControllerView'">컨트롤러</button>
          <button v-on:click="currentTab = 'ReserveView'">예약</button>
          <button v-on:click="currentTab = 'HistoryView'">이력</button>
        </div>
        <component
          v-bind:is="currentTabComponent"
          :userArr="userArr"
          :bathState="bathState"
          :toast="toast"
          @inputTemp="onChagebathTemp"
          @inputLevel="onChagebathLevel"
          :myreserveArr="myreserveArr"
          @getMyReserve="getMyReserve"
          @sendReserveDelete="sendReserveDelete"
          @onPopup="onPopup"
          @repeatRefresh="repeatRefresh"
          @saveUserLog="saveUserLog"
        ></component>
      </div>
    </div>
  </div>
</template>

<script>
import ControllerView from "@/views/ControllerView.vue";
import HistoryView from "@/views/HistoryView.vue";
import ReserveView from "@/views/ReserveView.vue";
import ReservePopup from "./components/ReservePopup.vue";
import { useToast } from "vue-toastification";

//새로고침 함수
var refreshInterval;
//새로고침 취소 함수
var refreshCancel;

export default {
  name: "HomeView",
  components: {
    ControllerView,
    HistoryView,
    ReserveView,
    ReservePopup,
  },
  mounted() {
    console.log(this.$bathid)
    this.getBathState();
    this.getReserveAllList();
    this.getMyReserve();
  },
  setup() {
    const toast = useToast();
    var historyArr = [];
    var allreserveArr = [];
    var allreserveArrKey = [];

    return {
      toast,
      historyArr,
      allreserveArr,
      allreserveArrKey,
    };
  },
  data() {
    return {
      //유저 정보 저장 배열(수정 필요)
      userArr: [{ img: "user" }],
      bathbuttonIcon_spin: "", //빈칸 아니면 fa-spin
      currentTab: "ControllerView",
      isReservePopup: false,
      //목욕 온도, 수위
      bathTemp: 40,
      bathLevel: 30,
      myreserveArr: Object,
      
      //bathImg : require('@/assets/img/bath_empty.png'),

      //욕조 상태 새로고침한 시간
      updateTime: " ",
      userName:this.$userName,

      //욕조 상태 변수(axios로 값 받아오기)
      bathState: {
        state: 0, //욕조 명령 상태 (0 아무 동작안함, 1 목욕전청소, 2 물받기,  3 물 유지)
        temp: 0,
        level: 0,
        cap: 1,
        h_valve: 0,
        c_valve: 0,
        clean_valve: 0,
      },
    };
  },
  computed: {
    //인자나 http코드가 있으면 methods 써라
    currentTabComponent() {
      return this.currentTab;
    },
    bathbuttonText() {
      if (this.bathState.state == 0) {
        return "목욕 시작";
      } else {
        return "목욕 종료";
      }
    },
    bathbuttonIcon() {
      if (this.bathState.state == 0) {
        return "fa-play";
      } else {
        return "fa-stop";
      }
    },
    bathStateText() {
      switch (this.bathState.state) {
        case 0:
          return "목욕 명령을 기다리고 있어요";
        case 1:
          return "욕조 청소 중";
        case 2:
          return "물 받는 중(" + this.bathState.level + "%)";
        case 3:
          return "목욕 대기 중";
      }
    },
    bathImg(){
      //return  require('@/assets/img/bath_empty.png')
      switch (this.bathState.state) {
        case 0:
          return  require('@/assets/img/bath_empty.png')
        case 1:
          return  require('@/assets/img/bath_empty.png')
        case 2:
          return  require('@/assets/img/bath_full.png')
        case 3:
          return  require('@/assets/img/bath_full.png')
        default:
          return require('@/assets/img/bath_empty.png')
      }
    },
  },
  methods: {
    //테스트 메소드
    test(){
      this.sendAlim("테스트 제목", "김성민이 테스트를 시작합니다.");

    },
    imgChange(){
      console.log("sdsd");
      this.bathState.state = 3;
    },

    //팀플 이력 화면 불러오기 API
    showHistoryList(){
      const data = {
              position: {
                right: '0',
                top: '0'
              }
            };
      iframeService.openMyHistoryListPop(data)
    },
    
    goMankik(){
      window.location.href="https://mzoin.com/mankik/appMain"
    },

    //팀플 이력 및 알림 성공 및 실패 메시지 콜백 함수 -->실패 시 실패 토스트 띄우기
    teamplCallback(data){
      console.log(data)
      if(data.result != true){
        this.toast.error("오류가 발생하여 이력이 저장되지 않았습니다.")
      }
    },

    //팀플 이력 API
    //logType-- BCLEANS_
    async saveUserLog (logType) {
      const param = {}
      param.logType = logType // 각앱별 리스트업 한 이력코드 입력
      param.targetKey = 1 //바스 앱 키 ==> 
      param.targetKind = 'A' // A: app , C: cabinet(가족), U: user
      param.frUserKey = this.$userKey //최초진입시 user > userKey로 저장
      param.frUserText = this.$userName

      // 욕조청소에는 toUser가 필요없음  
      // param.toUserKey = 3  
      // param.toUserText = '만이'
      mkFunctions.saveUserLog(param, this.teamplCallback)
    },

    //팀플 어플리케이션 알림 API
    async sendAlim (alarmTitle, alarmContent) {
      const accessObj = {accessKind: 'U', accessKey: this.$userKey}
      // // 가족(cabinetKey= 1)에게 보내는 알림
      // accessObj.accessKind = 'C'
      // accessObj.accessKey = 1
      const param = {}
      param.title = alarmTitle //'욕조청소알림'
      param.bodyFullStr = alarmContent//'20시 욕조청소가 예약되었습니다.'
      param.actorList = [accessObj]
      param.creAppKey = 1 //바스
      param.creUserKey = this.$userKey
      mkFunctions.sendAlim({contents: param}, this.teamplCallback) //callback: bath에서 정의한 resultReturn함수
    },

    //주기적으로 애플리케이션 새로고치는 함수
    repeatRefresh() {
      this.getBathState();
      this.getReserveAllList();
      this.getMyReserve();
      
      if(refreshInterval){
        clearTimeout(refreshInterval);
        clearTimeout(refreshCancel);
      }

      refreshInterval = setInterval(() => {
        this.getBathState();
        this.getReserveAllList();
        this.getMyReserve();
      }, 3000);

      refreshCancel = setTimeout(function () {
        clearTimeout(refreshInterval);
      }, 10000);
    },

    //목욕시작, 목욕 종료 버튼 클릭 시
    bathButtonClick() {
      if (this.bathState.state == 0) {
        this.sendBathStart();
        console.log(this.bathTemp + "/" + this.bathLevel);
      } else {
        this.sendBathFinish();
      }
    },
    
    
    //controllview에서 온도값 변경했을 시 수행하는 함수
    onChagebathTemp(val) {
      this.bathTemp = val;
    },
    //controllview에서 수위값 변경했을 시 수행하는 함수
    onChagebathLevel(val) {
      this.bathLevel = val;
    },
    //목욕 시작 명령 API
    sendBathStart() {
      this.$http
        .post("http://13.125.231.37:8080/control/" + this.$bathid, {
          cap: 0,
          h_valve: 0,
          c_valve: 0,
          clean: 15,
          user_id: this.$userKey,
          temp: this.bathTemp, //슬라이더 값으로
          level: this.bathLevel, //슬라이더 값으로
        })
        .then((res) => {
          console.log(res);
          this.toast("목욕을 시작합니다.");
          this.repeatRefresh();
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //목욕 종료 명령 API
    sendBathFinish() {
      this.$http
        .post("http://13.125.231.37:8080/control/" + this.$bathid, {
          cap: 0,
          h_valve: 0,
          c_valve: 0,
          clean: 0,
          user_id: this.$userKey,
          temp: 99,
          level: 0,
        })
        .then((res) => {
          console.log("목욕 종료 메시지");
          console.log(res);
          this.toast("목욕을 종료합니다.");
          this.repeatRefresh();
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //바스 기기 모든 사용자의 예약 리스트 가져오기 API
    getReserveAllList() {
      this.$http
        .get("http://13.125.231.37:8080/schedule/find/bath/" + this.$bathid, {}) //bathid
        .then((res) => {
          console.log(res);
          this.allreserveArr = res.data;
          this.allreserveArrKey = Object.keys(this.allreserveArr);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //사용자 예약 리스트 가져오기 API
    getMyReserve() {
      this.$http
        .get("http://13.125.231.37:8080/schedule/find/user/" + this.$userKey, {}) //userid
        .then((res) => {
          console.log(res);

          this.myreserveArr = Object.entries(res.data).sort((a, b) =>
            a[1].starttime.localeCompare(b[1].starttime)
          );
          console.log(this.myreserveArr);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //현재 욕조 상태 가져오기 API
    getBathState() {
      var today = new Date();
      var year = today.getFullYear();
      var month = ("0" + (today.getMonth() + 1)).slice(-2);
      var day = ("0" + today.getDate()).slice(-2);
      var dateString = year + "-" + month + "-" + day;
      var hours = ("0" + today.getHours()).slice(-2);
      var minutes = ("0" + today.getMinutes()).slice(-2);
      var seconds = ("0" + today.getSeconds()).slice(-2);
      var timeString = hours + ":" + minutes + ":" + seconds;

      this.$http
        .get("http://13.125.231.37:8080/bath/" + this.$bathid) //bathid
        .then((res) => {
          console.log(res.data);
          this.bathState.state = res.data.state;
          this.bathState.temp = res.data.temp;
          this.bathState.level = res.data.level;
          this.bathState.cap = res.data.cap;
          this.bathState.h_valve = res.data.h_valve;
          this.bathState.c_valve = res.data.c_valve;
          this.bathState.clean_valve = res.data.clean_valve;

          this.updateTime = dateString + " " + timeString + "에 불러옴";
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //예약 명령 API
    sendReserve(popuptemp, popuplevel, time) {
      this.$http
        .post("http://13.125.231.37:8080/schedule/" + this.$bathid, {
          user_id: this.$userKey,
          temp: popuptemp,
          level: popuplevel,
          clean_time: 15,
          bath_start: time,
        })
        .then((res) => {
          console.log(res);

          this.toast("목욕이 예약되었습니다.");
          this.repeatRefresh();
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //예약 취소 명령 API
    sendReserveDelete(index) {
      this.$http
        .delete("http://13.125.231.37:8080/schedule/delete/" + index)
        .then((res) => {
          this.toast("예약이 취소되었습니다.");
          this.repeatRefresh();
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //팝업 출력
    onPopup() {
      this.isReservePopup = true;
    },
    //팝업 종료
    offPopup() {
      this.isReservePopup = false;
    },
  },
};
</script>

