<template>
  <div>
    <div>
      <div id="btn_div">
        <font-awesome-icon
          :icon="['fas', 'plus']"
          id="plusBtn"
          v-on:click="onPopup"
        />
      </div>
      <div id="list-div">
        <div id="list-title">예약</div>
        <div id="empty-text" v-show="Object.keys(this.myArr).length === 0">
          {{ emptyText }}
        </div>
        <div v-for="(item, index) in myArr" :key="index" v-show="myArr">
          <div class="reserveItem">
            <div class="flex-div time-div">
              <div class="flex-div time-div-div">
                <span class="ampm-text">{{
                  ampmText(item[1].starttime.substr(11, 2))
                }}</span>
                <span class="time-text">{{
                  timeText(
                    item[1].starttime.substr(11, 2),
                    item[1].starttime.substr(14, 2)
                  )
                }}</span>
              </div>
              <span class="date-text">{{ dateText(item[1].starttime) }}</span>
            </div>

            <div class="delete-btn">
              <font-awesome-icon
                icon="xmark"
                v-on:click="sendReserveDelete(item[0])"
              />
            </div>
          </div>
          <hr />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {},
  setup() {
    const todayyear = new Date().getFullYear();
    var arrDayStr = ["일", "월", "화", "수", "목", "금", "토"];

    return { todayyear, arrDayStr };
  },
  data() {
    return {
      myArr: this.myreserveArr,
    };
  },
  computed: {
    emptyText() {
      if (Object.keys(this.myArr).length !== 0) {
        return "";
      } else {
        return "예약이 비어있어요!";
      }
    },
  },
  methods: {
    ampmText(hour) {
      if (Number(hour) < 12) {
        return "오전";
      } else {
        return "오후";
      }
    },
    timeText(hour, minute) {
      var h = Number(hour);
      if (h > 12) {
        h -= 12;
      }
      return h + ":" + minute;
    },
    dateText(starttime) {
      var year = starttime.substr(0, 4);
      var month = starttime.substr(5, 2);
      var day = starttime.substr(8, 2);
      var week = new Date(starttime).getDay();
      if (year != this.todayyear) {
        return (
          year +
          "년 " +
          month +
          "월 " +
          day +
          "일 (" +
          this.arrDayStr[week] +
          ")"
        );
      } else {
        return month + "월 " + day + "일 (" + this.arrDayStr[week] + ")";
      }
    },
    onPopup() {
      this.$emit("onPopup");
    },
    getMyReserve() {
      this.$emit("getMyReserve");
    },
    sendReserveDelete(index) {
      this.$emit("sendReserveDelete", index);
    },
  },
  props: {
    myreserveArr: {},
    toast: {},

  },
  watch: {
    //동적 props를 위해 사용
    myreserveArr() {
      this.myArr = this.myreserveArr;
    },
  },
};
</script>

<style scoped>
#btn_div {
  display: flex;
  flex-direction: row-reverse;

  margin: 0.625rem 0;
}

#plusBtn {
  font-size: 1.5rem;
  padding: 0.3rem;
  border-radius: 25px;
}
#plusBtn:hover{
  background-color: rgba(0, 0, 0, 0.226);
}

#list-div {
  padding: 0.5rem;
  background-color: white;
  border-radius: 10px;
}
#list-title {
  font-weight: bold;
  margin: 0.5rem;
}

#empty-text {
  font-weight: bold;
  text-align: center;
  height: 50vmin;
  line-height: 50vmin;
}

.reserveItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}
.flex-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.time-div {
  flex-grow: 2;
}

.ampm-text {
  font-size: 0.75rem;
  margin-right: 0.375rem;
}
.time-text {
  font-size: 1.5rem;
}
.date-text {
  font-size: 0.75rem;
  margin-right: 0.75rem;
}
.delete-btn{
  padding: 0.2rem;
  border-radius: 25px;
}

.delete-btn:hover{
  background-color: rgba(0, 0, 0, 0.226);
}
</style>